export default {
	data() {
		return {
			token: '',
			enjoyQrcode: [], //收藏列表
			isEmpty:null,
			pageIndex:1,
			end:false 
		}
	},
	onLoad: function() {
		this.token = uni.getStorageSync('token')
	},
	onShow: function() {
		this.getEnjoyList('1')
	},
	onReachBottom: function () {
		if(!this.end){
			this.pageIndex ++;
			this.getEnjoyList('2')
		}
	},
	methods: {
		//获取收藏列表
		getEnjoyList:function(type){
			var data = {
				pageIndex:this.pageIndex,
				pagec:10,
				token:this.token
			}
			this.$urlRequest('getFavoriteProdByUserId.do',data).then(res=>{
				if(type =='1'){
					if(res.data.length == 0){
						this.isEmpty = true
					}else{
						this.isEmpty = false
					}
				}
				if(this.pageIndex == 1){
					this.enjoyQrcode = res.data
				}else{
					if(res.data.length == 0){
						this.$myToast('暂无更多收藏')
						this.end = true
					}else{
						this.enjoyQrcode.push(res.data)
					}
				}
			})
		},
		//取消收藏
		delEnjoy:function(prodid){
			
			uni.showModal({
				title:'提示',
				content: '确定对该商品取关?',
				success:(res=>{
					if (res.confirm) {
						this.$urlRequest('deleteFavoriteProd.do',{token:this.token,id:prodid}).then(res=>{
							if(res.code == '1'){
								this.$myToast('已取消收藏')
							}else{
								this.$myToast(res.msg)
							}
							this.getEnjoyList()
						})
					} else if (res.cancel) {
					}
				})
			})
			
			
		},		
		//前往商品详情
		gotoProdDetail:function(prodid){
			uni.navigateTo({
				url:'/pages/prodDetail/prodDetail?prodId=' + prodid
			})
		}
	}
}
